












import Vue from "vue";
import Header from "./components/header/index.vue";
import Loader from "./components/LoadingOverlay.vue";

export default Vue.extend({
  name: "App",
  components: {
    Header,
    Loader
  },
  created() {
    const loader = document.getElementById("loader");
    if (loader) {
      loader.style.display = "none";
    }
  }
});
