


























import Vue from "vue";

export default Vue.extend({
  name: "Intro",
  props: {
    animate: Boolean
  },
  data: () => ({
    animateFirst: false,
    animateSecond: false,
    animateThird: false,
    animatedDescription: false,
    animateLogo: false
  }),
  mounted() {
    setTimeout(() => {
      this.animateFirst = true;
    }, 1000);
    setTimeout(() => {
      this.animateSecond = true;
      this.animateLogo = true;
      setTimeout(() => {
        this.animateLogo = false;
      }, 4000);
    }, 1500);
    setTimeout(() => {
      this.animateThird = true;
    }, 2000);
    setTimeout(() => {
      this.animatedDescription = true;
    }, 2500);
  }
});
